import React from 'react';
import { Link } from "gatsby";
import './navigation.component.css';

const toRouteObject = (name, to, enabled) => ({
  name,
  to,
  enabled,
});

const routes = [
  toRouteObject('Home', '/', true),
  toRouteObject('Map', '/map', true),
  toRouteObject('Home', '/experience', false),
];

const NavigationComponent = () => (
  <nav className="custom-nav navbar navbar-expand-lg navbar-light">
    <a className="navbar-brand" href="#">Bit-Dragon</a>

    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>

    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav mr-auto">
        {routes.map((route) => {
           if (!route.enabled) {
             return null;
           }

           return (
             <li className="nav-item active">
               <Link className="nav-link" to={route.to}>{route.name}</Link>
             </li>
           );
        })}
      </ul>
    </div>
  </nav>
);

export default NavigationComponent;
